// EntryCard.js
import React from "react";

export default function EntryCard(props) {
  const { item, type } = props;

  return (
    <div className="entry-card">
      <h4>{type === "education" ? item.degree : item.title}</h4>
      <h5>{type === "education" ? item.school : item.company}</h5>
      <p className="date-range">{item.date}</p>
      <ul>
        {(type === "education" ? item.courses : item.responsibilities).map(
          (entry, index) => (
            <li key={index}>{entry}</li>
          )
        )}
      </ul>
    </div>
  );
}
